<template>
  <div>
    <div class="vld-parent">
        <loading :active.sync="isLoading"></loading>
    </div>
    <div class="text-right mt-4">
            <button class="btn btn-primary" @click="openModal(true,'')"></button>
        </div>
    <table class="table mt-4">
        <thead>
            <th width="80"></th>
            <th width="120"></th>
            <th width="120"></th>
            <th width="120"></th>
            <th width="100"></th>
            <th width="80"></th>
        </thead>
        <tbody>
            <tr v-for="(item, key) in products" :key="key">
                <td>{{item.due_date}}</td>
                <td>{{item.title}}</td>
                <td>{{item.code}}</td>
                <td>
                {{item.percent}}
                </td>
                
                <td>
                    <span v-if="item.is_enabled" class="text-success"></span>
                    <span v-else></span>
                </td>
                <td>
                    <button class="btn btn-outline-primary btn-sm" @click="openModal(false,'', item)"></button>
                    <button class="btn btn-outline-danger btn-sm" @click.prevent="delModal(item)"></button>
                </td>
            </tr>
        </tbody>
    </table>
    <pagination :author="pagination" v-on:increment="getList"></pagination>
    <!--Modal-->
        <div class="modal fade" id="CouponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-dark text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                        <div class="col-12">
                            <div class="form-group">
                                <label for="coupon-title"></label>
                                <input type="text" class="form-control" id="coupon-title"
                                    placeholder="" v-model="tempProduct.title">
                            </div>
                        </div>
                        
                        <div class="col-12">
                            <div class="form-group">
                                <label for="coupon-code"></label>
                                <input type="text" class="form-control" id="coupon-code"
                                    placeholder="" v-model="tempProduct.code">
                            </div>
                        </div>
                        <div class="col-12">
                            <div class="form-group">
                                <label for="Duedate"></label>
                                <input class="form-control" type="date" id="Duedate" placeholder="2014-09-18" v-model="tempProduct.due_date">
                            </div>
                        </div>
                        <div class="col-12">
                            <div class="form-group">
                                <label for="percent"></label>
                                <input type="text" class="form-control" id="percent"
                                    placeholder="" v-model="tempProduct.percent">
                            </div>
                        </div>
                        <div class="col-12">
                            <div class="form-check">
                                <input class="form-check-input" type="checkbox"
                                id="is_enabled" v-model="tempProduct.is_enabled" :true-value="1" :false-value="0">
                                <label class="form-check-label" for="is_enabled">
                                
                                </label>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-primary" @click="updateCoupon"></button>
                </div>
                </div>
            </div>
        </div>
        <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                     <strong class="text-danger">{{ tempProduct.title }}</strong> ()
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-danger" @click="delCoupon"></button>
                </div>
                </div>
            </div>
        </div>
        <!--Modal-->

  </div>
</template>

<script>
import $ from 'jquery';
import pagination from './Pagination';
export default {
    name: 'Coupon',
    data(){
        return{
            tempProduct:{},
            products:[],
            pagination:{},
            isNew:false,
            isLoading:false,
        }
    },
    methods:{
            getList(page = 1){
                const vm = this;
                const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupons?page=${page}`;
                vm.isLoading = true;
                this.$http.get(api).then((response) => {
                    console.log('api', response);
                    vm.isLoading = false;
                    vm.pagination = response.data.pagination;
                    vm.products = response.data.coupons;   
                })
            },
            openModal(isNew, content, item){
                const vm = this;
                $('#CouponModal').modal('show');
                if(isNew){
                    vm.tempProduct = {};
                    vm.isNew = true;
                } else {
                    vm.tempProduct = Object.assign({}, item);
                    this.isNew = false;
                }
                $('#CouponModal').find('.modal-title').text(content)
            },
            delModal(item){
                $('#delCouponModal').modal('show');
                this.tempProduct = Object.assign({},item);
            },
            delCoupon(){
                const vm = this;
                const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempProduct.id}`;
                this.$http.delete(api).then((response) => {
                    if(response.data.success){
                        console.log('');
                        $('#delCouponModal').modal('hide');
                        
                    } else{
                        console.log('');
                        $('#delCouponModal').modal('hide');
                        
                    }
                    vm.getList();
                    
                })
            },
            updateCoupon(){
                const vm = this;
                let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon`;
                let httpMode = 'post';
                vm.isLoading = true;
                if(!vm.isNew){
                    api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempProduct.id}`;
                    httpMode = 'put';
                }
                this.$http[httpMode](api, {data:vm.tempProduct}).then((response) => {
                   if(response.data.success){
                        vm.isLoading = false;
                       $('#CouponModal').modal('hide');
                       console.log('');
                       
                   } else {
                        $('#productModal').modal('hide');
                        
                        console.log('');
                    } 
                    vm.getList();
                })
                
               
            }
    },
    created(){
      this.getList();
    },
    components:{
        pagination
    }
};
</script>

<style scope>

</style>