<template>
    <div>
        <Navbar :product_num="product_length" v-on:increment="CounterCoupute"></Navbar>
        <nav aria-label="breadcrumb">
            <ol class="breadcrumb bg-transparent border border-bottom">
                <li class="breadcrumb-item">
                    <router-link to="/">
                        <a href="#" class="text-muted"></a>
                    </router-link>
                </li>
                <li class="breadcrumb-item active text-dark" aria-current="page"></li>
            </ol>
        </nav>
        <div class="row px-3">
          <div class="col-sm-4">
            <div class="form-group">
              <select class="form-control" v-model="selectlocation" @change="PenTo(selectlocation)">
                <option v-for="(item, key) in data" :key="key" :value="item">{{item[""]}}</option>
              </select>
            </div>
            <div>
              <h5 class="font-weight-bold">
                {{selectlocation[""]}}
              </h5>
              <small class="text-muted">{{selectlocation[""]}}</small>
              <br>
              <h5 class="mt-3" v-if="selectlocation.decription!==''">{{selectlocation.decription}}</h5>
              <img :src="selectlocation.image" alt="..." class="img-thumbnail" v-if="selectlocation.image!==''">
            </div>
          </div>
          <div class="col-sm-8">
            <div id="map"></div>
          </div>
        </div>
    </div>
</template>


<script>
  import Navbar from './Navbar'
  import L from 'leaflet' 
  let osmMap = {} 
  const storeTile = ['Berserker ', 'Berserker ', 'Berserker ', 'Berserker ', 'Berserker ', 'Berserker ', 'Berserker ', 'Berserker ',]
  const storeImage = ['https://upload.cc/i1/2020/12/24/Rxfch5.jpg','https://upload.cc/i1/2021/02/18/CTu2Xn.jpg','https://upload.cc/i1/2021/02/18/SbnF9j.jpg','https://upload.cc/i1/2021/02/18/cyrh8S.jpg',
  'https://upload.cc/i1/2021/02/18/xmQ32T.jpg','https://upload.cc/i1/2021/02/18/hqHAcb.jpg','https://upload.cc/i1/2021/02/18/6YeM1q.jpg','https://upload.cc/i1/2021/02/18/8OrZQj.jpg']
  const storedecription = ['', '', '', '', '', '',
  '', '']
  
  export default {
    data() {
      return {
        product_length:0,
        data:[],
        selectlocation:{
          :'Berserker ',
          image:'https://upload.cc/i1/2021/02/18/CTu2Xn.jpg',
          :'99',
          decription:''
        }
      }
    },
    methods: {
      CounterCoupute(cart_total_length){
        this.product_length = cart_total_length;
      },
      updateMap () {
        this.data.forEach(item => {
          L.marker([item[""], item[""]]).addTo(osmMap).bindPopup(`<strong>:${item['']}</strong><br>
          <strong>:<a href="https://www.google.com.tw/maps/place/${item['']}" target="_blank">${item['']}</a></strong><br>
          <strong>:${item['']}</strong>`)
        })
       
        osmMap.panTo(new L.LatLng(24.130993, 120.721122));
        L.marker([24.130993, 120.721122]).addTo(osmMap).bindPopup(`<strong>:Berserker </strong><br>
          <strong>:<a href="https://www.google.com.tw/maps/place/99" target="_blank">99</a></strong><br>
          <strong>:04-22289111#51311</strong>`).openPopup();
        
      },
      PenTo(location){
        osmMap.panTo(new L.LatLng(location[""], location[""]));
        L.marker([location[""], location[""]]).addTo(osmMap).bindPopup(`<strong>:${location['']}</strong><br>
          <strong>:<a href="https://www.google.com.tw/maps/place/${location['']}" target="_blank">${location['']}</a></strong><br>
          <strong>:${location['']}</strong>`).openPopup();
        
      }
    },
    components:{
        Navbar
    },
    mounted(){
      let vm = this;
      let xhr = new XMLHttpRequest();
      xhr.open('GET', 'https://datacenter.taichung.gov.tw/swagger/OpenData/8f477165-6874-4856-b60d-38029958e723', true);
      xhr.withCredentials = false;
      xhr.send(null);
      xhr.onload = function(){
        vm.data = xhr.response
        vm.data = JSON.parse(xhr.response)
        
        for(let i=0; i!==vm.data.length; i++){
          vm.data[i].image = storeImage[i];
          vm.data[i][""] = storeTile[i];
          vm.data[i].decription = storedecription[i];
        }
        console.log('', vm.data);
        vm.updateMap();
      }
      
      osmMap = L.map('map', {
        center: [24.130993, 120.721122],
        zoom: 10
      })  
      L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        attribution: '<a target="_blank" href="https://www.openstreetmap.org/"> OpenStreetMap </a>',
        maxZoom: 18
      }).addTo(osmMap)   

    }
  }
</script>

<style scoped>
#map {
  height: 100vh;
}
</style>