<template>
    <div>
        <div class="vld-parent">
            <loading :active.sync="isLoading"></loading>
        </div>
        <table class="table mt-4">
            <thead>
                <th width="120"></th>
                <th width="100"></th>
                <th></th>
                <th width="100"></th>
            </thead>
            <tbody>
                <tr v-for="(item, key) in customer_order" :key="key">
                    <td>{{item.user.name}}</td>
                    <td>{{item.user.email}}</td>
                    <td>
                        <span v-for="(obj,index) in item.products">{{obj.product.title}}, </span>
                    </td>
                    <td>
                        <span v-if="item.is_paid" class="text-success"></span>
                        <span v-else class="text-danger"></span>
                    </td>
                </tr>
            </tbody>
        </table>
        
        <pagination :author="pagination" v-on:increment="getProducts"></pagination>
    </div>
</template>

<script>
import $ from 'jquery'
import Navbar from './Navbar'
import pagination from './Pagination'
export default {
  data () {
      return {
        pagination:{},
        products:[],
        tempProduct:{},
        isNew: false,
        isLoading: false,
        Status:{
            isUploading:false
        },
        customer_order:[]
      }
  },
  methods:{
        getProducts(page=1){
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/orders?page=${page}`; 
            vm.isLoading = true;
            this.$http.get(api).then((response) => {
                vm.isLoading = false;
                
                console.log('',response.data);
                let el = `"${response.data.orders[0].id}"`;
                console.log('id',el);
                console.log('', response.data.orders[0].products);
                
                vm.customer_order = response.data.orders;
                vm.pagination = response.data.pagination;
            })
        },
        
    },
    created(){
        this.getProducts()
    },
    components:{
        pagination,
        Navbar
    }
}
</script>